/* Card **********************/
/****************************   */
.card {
  --fs-card-background-color: #0dcaf0;
  --fs-card-box-shadow: 0 10px 30px -15px rgba(13, 202, 240, 0.75);
  --fs-card-border-radius: 4px;
  --fs-card-padding-vertical: 1rem;
  --fs-card-padding-horizontal: 0.5rem;
  --fs-card-color: var(--fs-primary-color);
  padding: var(--fs-card-padding-vertical) var(--fs-card-padding-horizontal);
  border-radius: var(--fs-card-border-radius);
  background-color: var(--fs-card-background-color);
  box-shadow: var(--fs-card-box-shadow);
  color: var(--fs-card-color);
}
.card .card-title {
  --fs-card-title-color: var(--fs-card-color);
  color: var(--fs-card-title-color);
}
.card .card-content {
  --fs-card-content-color: var(--fs-card-color);
  color: var(--fs-card-content-color);
}
.card:is([aria-current], :hover, :active, :focus) {
  --fs-card-box-shadow: 0 10px 30px -5px rgba(13, 202, 240, 0.85);
}

/* Button **********************/
.btn-outline {
  --fs-background-color: transparent;
  --fs-btn-color: #0dcaf0;
}
.btn-outline:is([aria-current], :hover, :active, :focus) {
  --fs-btn-color: var(--fs-primary-color);
}
.btn-text {
  --fs-background-color: transparent;
  --fs-btn-border-width: 0;
  --fs-btn-color: #0dcaf0;
  --fs-btn-box-shadow: none;
  --fs-btn-border-radius: 0;
  border-bottom: 1px solid var(--fs-btn-border-color);
}
.btn-text:is([aria-current], :hover, :active, :focus) {
  --fs-background-color: transparent;
  --fs-btn-box-shadow: none;
  --fs-btn-color: #0aa2c0;
  --fs-btn-border-color: #0aa2c0;
}

